import axios from 'axios';
import Global from '../../../infrastructure/Common/Global';

class AdminApis {

    adminSettings() {
        return axios.get(Global.adminSettings, Global.httpOptions);
    }

    setAdminSettings(data) {
        return axios.post(Global.adminSettings, data, Global.httpOptions);
    }

    cmsContent() {
        return axios.get(Global.cmsContent, Global.httpOptions);
    }

    savePost(type, data) {
        
        const headers = {headers: {...Global.httpOptions.headers, 'Content-type': 'multipart/form-data'}};
        
        // console.log('headder', headers);
        
        return axios.post(
            type === 'update' ? Global.updatePost : Global.newPost,
            data,
            headers
        )
    }

    cmsPosts(data) {
        return axios.post(Global.cmsPosts, data, Global.httpOptions);
    }

    postDetail(data) {
        return axios.post(Global.postDetail, data, Global.httpOptions);
    }

    updatePostStatus(data) {
        return axios.post(Global.postStatus, data, Global.httpOptions);
    }

}

// export default new AdminApis();
export default new AdminApis();