import React, { useEffect, useState } from 'react';

import { useTranslation } from 'react-i18next';

import AdminApis from '../APIs/AdminApis';
import Notifier from '../../../application/utils/Notifier';

import './settings.scss';

const Settings = () => {
    const { t } = useTranslation();

    const [activeTab, setActiveTab] = useState('general');

    const settingsTabs = [
        { id: 'general', name: `${t('general')}` },
        { id: 'footer', name: `${t('footer')}` }
    ];
    
    const [disableInputGeneral, setDisableInputGeneral] = useState(true);
    const [disableInputFooter, setDisableInputFooter] = useState(true);
    const [isLoading, setIsLoading] = useState(true);
    const [isSaving, setIsSaving] = useState(false);

    const [settingsData, setSettingsData] = useState({});

    useEffect(() => {
        getAdminSettings();
    }, []);

    const getAdminSettings = () => {
        // setIsLoading(true);
        AdminApis.adminSettings()
            .then(res => {
                setSettingsData(res.data.result);
                setIsLoading(false);
            })
            .catch(err => {
                console.log(err);
                setIsLoading(false);
            })
    };

    const handleChange = e => {
        const { name, value } = e.target
        setSettingsData({
            ...settingsData,
            [name]: value
        });
    };

    const handleSubmitGeneral = e => {
        e.preventDefault();
        setIsSaving(true);

        const formData = {
            contact_number: settingsData.contact_number,
            contact_email: settingsData.contact_email,
            address: settingsData.address,
        }

        AdminApis.setAdminSettings(formData)
            .then(res => {
                setIsSaving(false);
                Notifier.notify('success', res.data.result);
            })
            .catch(err => {
                console.log(err);
                setIsSaving(false);
                Notifier.notify('error', err.response.data.result);
            })
    };

    const handleSubmitFooter = e => {
        e.preventDefault();
        setIsSaving(true);

        const formData = {
            featured_text_en: settingsData.featured_text_en,
            featured_text_bg: settingsData.featured_text_bg,
            subscribe_text_en: settingsData.subscribe_text_en,
            subscribe_text_bg: settingsData.subscribe_text_bg,
            copyright_text_en: settingsData.copyright_text_en,
            copyright_text_bg: settingsData.copyright_text_bg,
            powered_by: settingsData.powered_by,
            powered_by_link: settingsData.powered_by_link,
            useful_links: '',
        };

        AdminApis.setAdminSettings(formData)
            .then(res => {
                setIsSaving(false);
                Notifier.notify('success', res.data.result);
            })
            .catch(err => {
                console.log(err);
                setIsSaving(false);
                Notifier.notify('error', err.data.result);
            })
    };

    return (
        <div className="container-fluid p-4">
            <div className="d-block d-md-flex align-items-end justify-content-between mb-4">
                <div className="section-header mb-3 mb-md-0">
                    <div className="primary">{t('settings')}</div>
                    <h5 className="title fw-medium mb-0">{t('settings_desc')}</h5>
                </div>
            </div>

            <section className="section-data p-3">
                {isLoading ?
                    <div className="content-placeholder">
                        <h5 className="placeholder-glow mb-4">
                            <span className="placeholder col-6"></span>
                        </h5>
                        <p className="card-text placeholder-glow">
                            <span className="placeholder col-7 mt-3"></span>
                            <span className="placeholder col-4 mt-3"></span>
                            <span className="placeholder col-6"></span>
                            <span className="placeholder col-6"></span>
                            <span className="placeholder col-6"></span>
                            <span className="placeholder col-3"></span>
                            <span className="placeholder col-6"></span>
                            <span className="placeholder col-2"></span>
                            <span className="placeholder col-7"></span>
                        </p>
                        {/* <a href="#" tabindex="-1" className="btn nodis-btn success disabled placeholder col-3 ms-auto"></a> */}
                    </div>
                    :

                    <div className="nodis-tabs-container">

                        <div className="nodis-tabs d-flex">
                            {settingsTabs.map((tab, index) => (
                                <div className={'tab-item me-4 ' + (tab.id === activeTab ? 'active' : '')} key={'tab-' + (index + 1)} onClick={() => setActiveTab(tab.id)}>
                                    {tab.name}
                                </div>
                            ))}
                        </div>

                        <div className="tabs-content mt-4">
                            {activeTab === 'general' &&
                                <div className="row">
                                    <div className="col-md-12">
                                        <form onSubmit={handleSubmitGeneral}>
                                            <div className="row">
                                                <div className="col-md-6">
                                                    <div className="mb-4">
                                                        <label htmlFor="contactNumber" className="form-label label-text">{t('contact_number')}</label>
                                                        <input type="text" className="form-control nodis-field " disabled={disableInputGeneral} id="contactNumber" aria-describedby="contactNumber" name="contact_number" defaultValue={settingsData.contact_number} onChange={handleChange} />
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="mb-4">
                                                        <label htmlFor="contactEmail" className="form-label label-text">{t('contact_email')}</label>
                                                        <input type="email" className="form-control nodis-field " disabled={disableInputGeneral} id="contactEmail" aria-describedby="emailHelp" name="contact_email" defaultValue={settingsData.contact_email} onChange={handleChange} />
                                                    </div>
                                                </div>
                                                <div className="col-md-12">
                                                    <div className="mb-4">
                                                        <label htmlFor="contactAddress" className="form-label label-text">{t('address')}</label>
                                                        <input type="text" className="form-control nodis-field " disabled={disableInputGeneral} id="contactAddress" aria-describedby="contactAddress" name="address" defaultValue={settingsData.address} onChange={handleChange} />
                                                    </div>
                                                </div>
                                                <div className="col-md-12">
                                                    <div className="row">
                                                        <div className="col-auto ms-auto" style={{ display: !disableInputGeneral ? "none" : "block" }}>
                                                            <button type="button" className="btn nodis-btn outline btn-block w-100" onClick={() => setDisableInputGeneral(false)} >{t('enable_edit')}</button>
                                                        </div>
                                                        <div className="col-auto ms-auto p-0" style={{ display: disableInputGeneral ? "none" : "block" }}>
                                                            <button type="button" className="btn nodis-btn outline btn-block w-100" onClick={() => setDisableInputGeneral(true)} >{t('cancel')}</button>
                                                        </div>
                                                        <div className="col-auto " style={{ display: disableInputGeneral ? "none" : "block" }}>

                                                            <button type="submit" className="btn nodis-btn success btn-block w-100" disabled={isSaving}>
                                                                {isSaving &&
                                                                    <div className="button-loader">
                                                                        {t('saving')}...
                                                                    </div>
                                                                }
                                                                {t('save_changes')}
                                                            </button>

                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </form>
                                    </div>
                                </div>
                            }

                            {activeTab === 'footer' &&
                                <div className="row">
                                    <div className="col-md-12">
                                        <form onSubmit={handleSubmitFooter}>
                                            <div className="row">
                                                <div className="col-md-6">
                                                    <div className="mb-4">
                                                        <label htmlFor="featuredTextEn" className="form-label label-text">{t('featured_text_en')}</label>
                                                        <textarea type="text" rows="5" className="form-control nodis-field " disabled={disableInputFooter} id="featuredTextEn" aria-describedby="featuredTextEn" name="featured_text_en" defaultValue={settingsData.featured_text_en} onChange={handleChange} />
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="mb-4">
                                                        <label htmlFor="featuredTextBg" className="form-label label-text">{t('featured_text_bg')}</label>
                                                        <textarea type="text" rows="5" className="form-control nodis-field " disabled={disableInputFooter} id="featuredTextBg" aria-describedby="featuredTextBg" name="featured_text_bg" defaultValue={settingsData.featured_text_bg} onChange={handleChange} />
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="mb-4">
                                                        <label htmlFor="copyrightTextEn" className="form-label label-text">{t('copyright_text_en')}</label>
                                                        <input type="text" className="form-control nodis-field " disabled={disableInputFooter} id="copyrightTextEn" aria-describedby="copyrightTextEn" name="copyright_text_en" defaultValue={settingsData.copyright_text_en} onChange={handleChange} />
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="mb-4">
                                                        <label htmlFor="copyrightTextBg" className="form-label label-text">{t('copyright_text_bg')}</label>
                                                        <input type="text" className="form-control nodis-field " disabled={disableInputFooter} id="copyrightTextBg" aria-describedby="copyrightTextBg" name="copyright_text_bg" defaultValue={settingsData.copyright_text_bg} onChange={handleChange} />
                                                    </div>
                                                </div>

                                                <div className="col-md-6">

                                                    <div className="mb-4">
                                                        <label htmlFor="poweredbyName" className="form-label label-text">{t('powered_by_name')}</label>
                                                        <input type="text" className="form-control nodis-field " disabled={disableInputFooter} id="poweredbyName" aria-describedby="poweredbyName" name="powered_by" defaultValue={settingsData.powered_by} onChange={handleChange} />
                                                    </div>

                                                </div>
                                                <div className="col-md-6">
                                                    <div className="mb-4">
                                                        <label htmlFor="poweredbyLink" className="form-label label-text">{t('powered_by_link')}</label>
                                                        <input type="text" className="form-control nodis-field " disabled={disableInputFooter} id="poweredbyLink" aria-describedby="poweredbyLink" name="powered_by_link" defaultValue={settingsData.powered_by_link} onChange={handleChange} />
                                                    </div>
                                                </div>
                                                <div className="col-md-12">
                                                    <div className="row">
                                                        <div className="col-auto ms-auto" style={{ display: !disableInputFooter ? "none" : "block" }}>
                                                            <button type="button" className="btn nodis-btn outline btn-block w-100" onClick={() => setDisableInputFooter(false)} >{t('enable_edit')}</button>
                                                        </div>
                                                        <div className="col-auto ms-auto p-0" style={{ display: disableInputFooter ? "none" : "block" }}>
                                                            <button type="button" className="btn nodis-btn outline btn-block w-100" onClick={() => setDisableInputFooter(true)} >{t('cancel')}</button>
                                                        </div>
                                                        <div className="col-auto " style={{ display: disableInputFooter ? "none" : "block" }}>

                                                            <button type="submit" className="btn nodis-btn success btn-block w-100" disabled={isSaving}>
                                                                {isSaving &&
                                                                    <div className="button-loader">
                                                                        {t('saving')}...
                                                                    </div>
                                                                }
                                                                {t('save_changes')}
                                                            </button>

                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </form>
                                    </div>
                                </div>
                            }
                        </div>

                    </div>
                }
            </section>
        </div>
    )
}

export default Settings;
